.SelectionMenu {
    z-index: 390;
    position: absolute;
    width: 200px;
    right: 0;
    top: 40px;
    bottom: 0;
    padding: 8px; 
    font-size: 8pt;
    background-color: #11192c;
}

.SelectionMenu > div {
    margin-bottom: 20px; 
    padding: 5px; 
    border-bottom: 1px solid #595f6d; 
}

.flex {
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 5px;
}

.flexSpaceBetween {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.inputWrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    font-size: 8pt;
    width: 50%; 
    margin-bottom: 10px; 
}

.inputWrapper span {
    margin-top: 4px; 
    margin-right: 4px; 
}

.input {
    width: 35px;
    border: none; 
    color: #ffffff;
    text-align: right;
    height: 20px; 
    background-color: #283557;
    box-shadow:  3px 3px 10px #00000080; 
    border-radius: 8px;
    padding: 2px 6px; 
} 

.input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.colorInputContainer {
    position: relative; 
    height: 50px;
    width: 100%; 
    border-radius: 50%; 
    margin-left: auto;
    margin-right: auto; 
}

.colorInputBackground {
    position: absolute; 
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 50px;
    width: 50px; 
    border-radius: 50%; 
    margin-left: auto;
    margin-right: auto; 
    border: 5px solid #11192c; 
    box-sizing: border-box;
    box-shadow: -3px -3px 10px #1F2C3F, 3px 3px 10px #000000;
}

.colorInput {
    position: relative;
    top: 0; 
    left: 0;
    opacity: 0;
    border-radius: 100%; 
    height: 100%;
    width: 100%;
    -webkit-appearance: none;
    border: 1px solid red; 
    cursor: pointer;
}

.colorOptions p {
    margin: 0;
    padding-top: 0;
}


.alignIcons {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.alignIcon {
    cursor: pointer;
    margin-bottom: 5px; 
    margin-right: 2px; 
}

.alignIcon svg {
    height: 25px;
    width: 25px;
}

.alignActive {
    background-color: #ffffff;
    border-radius: 3px;
}

.alignInactive:hover {
    background-color: #9d9ea1;
    border-radius: 3px;
}

.textBoxTypeOptions {
    text-align: left;
}

.fontSelector {
    margin-right: 9px;
    height: 21.5px; 
}

.fontSelector select {
    width: 120px;
    height: 21.5px; 
} 

.previewContainer {
    height: 190px; 
    width: 190px;
    position: relative; 
    margin-bottom: 20px; 
    background-color: #283557;
    box-shadow:  3px 3px 10px #00000080; 
    border-radius: 8px;}

.preview {
    height: 100%;
    width: 100%;
}