.ExportModal {
    height: 100%; 
    width: 100%;
    position: relative; 
    z-index: 1000; 
    margin-top: -40px;
}

.Background {
    background-color: #b5d1ffd0;
    height: 100%;
    width: 100%;
}

.Modal {
    height: 80%;
    width: 80%;
    /* height: 600px; */
    /* width: 800px; */
    position: absolute;
    left: 50%;
    top: 50%; 
    transform: translate(-50%, -50%);
    background-color: #11192c;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 20px;

}

.PreviewContainer {
    height: 100%;
    width: 80%;
    background-color: #474747;
    position: relative;
}

.Preveiw {
    position: absolute;
    overflow-y: scrolls;
    position: relative;
    background-color: #ffffff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.DownloadOptions {
    width: 20%;
}

.DownloadWrapper {
    height: inherit;
    background-color: #ffffff;
}